* {
  box-sizing: border-box;
}

.page {
  scroll-behavior: smooth;
}

.page__body {
  background-color: #f7f7f7;
  font-family: Inter, sans-serif;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: auto;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}

.page__body--with-menu {
  overflow: hidden;
}

@font-face {
  font-family: Inter, sans-serif;
  src: url("Inter-Regular.30288fd8.ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Inter, sans-serif;
  src: url("Inter-Bold.262a7633.ttf");
  font-weight: 700;
  font-style: normal;
}

h1, h2, h3, h4, h5, h6, p, a, body, li, ul, button {
  margin: 0;
  padding: 0;
}

.header__container {
  padding-inline: 30px;
}

@media (width >= 640px) {
  .header__container {
    padding-inline: 34px;
  }
}

@media (width >= 1280px) {
  .header__container {
    padding-inline: 123px;
  }
}

.header__top {
  justify-content: space-between;
  padding-block: 25px;
  display: flex;
}

@media (width >= 640px) {
  .header__top {
    padding-block: 40px;
  }
}

.header__top-wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__top-specs {
  text-transform: uppercase;
  color: #4f4f4f;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
  transition: all .3s;
  display: none;
}

@media (width >= 640px) {
  .header__top-specs {
    margin-right: 30px;
    display: block;
  }
}

.header__top-specs:hover {
  color: #333;
  font-weight: bolder;
}

.header__top-cta {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #333;
  width: 102px;
  padding-block: 16px;
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  transition: all .3s;
  display: none;
}

@media (width >= 640px) {
  .header__top-cta {
    margin-right: 30px;
    display: block;
  }
}

.header__top-cta:hover {
  background-color: #131313;
}

.header__top-menu {
  cursor: pointer;
  background-image: url("icon-burger.59e01678.svg");
  width: 32px;
  height: 32px;
  transition: transform .3s;
  display: block;
}

@media (width >= 1280px) {
  .header__top-menu {
    display: none;
  }
}

.header__top-menu:hover {
  transform: scale(1.09);
}

.header__top-link {
  padding-block: 12px;
}

.header__top-link-logo {
  width: 161px;
  height: 25px;
}

.header__top-link-logo:hover {
  transition: transform .3s;
}

.header__top-link-logo:hover:hover {
  transform: scale(1.05);
}

.header__top-buttons {
  display: none;
}

@media (width >= 640px) {
  .header__top-buttons {
    margin-right: 30px;
    display: block;
  }
}

@media (width >= 1280px) {
