}

.header__top-buttons-button {
  text-align: left;
  color: #bdbdbd;
  cursor: pointer;
  background-color: #f7f7f7;
  border: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.header__top-buttons-button.active {
  color: #000;
  font-weight: bold;
}

.header__middle {
  --columns: 4;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  margin-top: 50px;
  display: grid;
  overflow: hidden;
}

@media (width >= 640px) {
  .header__middle {
    --columns: 8;
    column-gap: 20px;
  }
}

@media (width >= 1280px) {
