    --columns: 12;
    column-gap: 30px;
  }
}

.header__middle-img {
  grid-column: 1 / -1;
  width: 100%;
  height: auto;
  transform: translate(15%);
}

@media (width >= 640px) {
  .header__middle-img {
    z-index: -1;
    grid-area: 1 / 4 / 3 / -1;
    align-self: center;
    transform: translate(50px);
  }
}

@media (width >= 1280px) {
  .header__middle-img {
    grid-area: 1 / 5 / 4 / -1;
  }
}

.header__middle-title {
  text-transform: uppercase;
